/* Custom Cyberpunk Scrollbar */
.custom-scrollbar::-webkit-scrollbar {
  width: 8px;
}

.custom-scrollbar::-webkit-scrollbar-track {
  background: rgba(0, 0, 0, 0.3);
  border-radius: 4px;
}

.custom-scrollbar::-webkit-scrollbar-thumb {
  background: linear-gradient(180deg, #00ffff, #00ff00);
  border-radius: 4px;
  box-shadow: 0 0 10px rgba(0, 255, 255, 0.5);
}

.custom-scrollbar::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(180deg, #00ff00, #00ffff);
  box-shadow: 0 0 15px rgba(0, 255, 0, 0.7);
}

/* Cyberpunk Glow Effects */
.cyber-glow {
  box-shadow: 0 0 20px rgba(0, 255, 0, 0.3);
}

.cyber-glow:hover {
  box-shadow: 0 0 30px rgba(0, 255, 0, 0.5);
}

/* Neon Text Effect */
.neon-text {
  text-shadow: 
    0 0 5px currentColor,
    0 0 10px currentColor,
    0 0 15px currentColor,
    0 0 20px currentColor;
}

/* Matrix-style background animation */
@keyframes matrix {
  0% { transform: translateY(-100vh); }
  100% { transform: translateY(100vh); }
}

.matrix-bg::before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: 
    linear-gradient(90deg, transparent 98%, rgba(0, 255, 0, 0.03) 100%),
    linear-gradient(180deg, transparent 98%, rgba(0, 255, 0, 0.03) 100%);
  background-size: 50px 50px;
  pointer-events: none;
  z-index: -1;
}

/* Pulse animation for status indicators */
@keyframes pulse-glow {
  0%, 100% { 
    opacity: 1;
    box-shadow: 0 0 5px currentColor;
  }
  50% { 
    opacity: 0.5;
    box-shadow: 0 0 20px currentColor;
  }
}

.pulse-glow {
  animation: pulse-glow 2s ease-in-out infinite;
}
